const joinLobby = function () {
    this.$store.dispatch("audio/playBlocked")
    alert("This isn't working yet, sorry!")
}

const joinRankedLobby = async function () {
    if (this.gameRoom !== null) {
        this.gameRoom.leave()
        return
    }

    if (!this.currentTeam.full) {
        this.$store.dispatch("audio/playBlocked")
        alert("You need exactly 3 characters to start a game!")
        return
    }
    this.searchingGame = true
    this.$store.dispatch("audio/playSnap")
    try {
        this.gameRoom = await this.$colyseus.joinOrCreate('rankedLobby', this.$store.getters['user/ranked'])
        this.gameRoom.onMessage('seat', async reservation => {
            try {
                const battleRoom = await this.$colyseus.consumeSeatReservation(reservation)
                this.$store.commit("game/setRoom", battleRoom)
                this.battleRoom.onMessage('joined', (msg) => {
                    if (Number(msg) === 2) {
                        this.battleRoom.send("start-game-data", {
                            team: this.currentTeam.members,
                            player: this.$store.getters['user/getUserData']
                        })
                    }
                })
                this.battleRoom.onMessage('game-started', (p) => {
                    this.$store.dispatch("audio/changeIntoBattle")
                    this.$store.commit('game/setGameState', p)
                    this.$router.push({ name: 'ingame' })
                })
                this.gameRoom.leave()
            } catch (err) {
                alert(err)
            }
        })
        this.gameRoom.onLeave(async () => {
            this.gameRoom = null
            this.searchingGame = false
        })
    } catch (error) {
        alert(error)
    }
}

const removeCharByIndex = async function (index) {
    if (this.searchingGame) return
    this.$store.commit('game/removeCharByIndex', index)
}

const handleDrop = function (index, transferData) {
    if (this.searchingGame) return
    if (!transferData) return
    this.$store.commit('game/addToTeamAtIndex', { char: transferData, index: index })
}

export default {
    joinLobby,
    handleDrop,
    joinRankedLobby,
    removeCharByIndex,
}